import styles from './CareerSection.module.scss'
import { Typography } from "../../../../Components/Typography"
import { mainbg, chevron } from "./Assets/Images"
export default function CareerSection() {
    return (
        <div>
            <section className={styles.mainSection}>
                <div style={{ backgroundImage: `url(${mainbg})` }} className={styles.titleContainer}>
                    <Typography variant='TypographySubtitle' color='white'>Est na hora de escolher a<br></br>
                        <span style={{ color: '#407BFF' }}>SUA CARREIRA</span></Typography>
                    <div className={styles.backgroundShadow}></div>
                </div>
                
                <div className={styles.careerContainer}>
                    <div>
                        <h3>Gestor de Negcios</h3>
                        <Typography variant='TypographyParagraph' >
                            Formar, de maneira efetiva, jovens administradores de empresa que tenham total aderncia aos nossos valores, alta capacidade de liderana, aguado tino comercial e competncia para produzir resultados superlativos.
                        </Typography>
                        <img src={chevron} alt="Seta indicativa para acessar o contedo" />
                    </div>
                    <div>
                        <h3>Programador</h3>
                        <Typography variant='TypographyParagraph' >
                            Formar, com excelncia, jovens Especialistas em Tecnologias Digitais que tenham total aderncia aos nossos valores, alta capacidade crtica, analtica e cognitiva, com competncias para desenvolver solues tecnolgicas inovadoras a fim de produzir resultados superlativos.
                        </Typography>
                        <img src={chevron} alt="Seta indicativa para acessar o contedo" />
                    </div>
                </div>
            </section>
        </div>
    )
}