import { Typography } from "../../../../Components/Typography"
import styles from './TopicText.module.scss'

export function TopicText() {
    return (
        <div className={styles.marginBottomDiv}>
            <div className={styles.divAdjust}>
                <Typography variant='TypographyParagraph' color='#323232' id={styles.boldAdjust}><li>Coleta de dados sensveis e coleta de dados de menor</li></Typography>
                <Typography variant='TypographyParagraph' color='#323232'>Em alguns casos, podemos realizar a coleta de alguns dados pessoais considerados como sensveis conforme definio da Lei Geral de Proteo de Dados (dado pessoal sobre origem racial ou tnica, convico religiosa, opinio poltica, filiao a sindicato ou a organizao de carter religioso, filosfico ou poltico, dado referente  sade ou  vida sexual, dado gentico ou biomtrico) ou a coleta de algum dado pessoal que possa vir a revelar algum dado sensvel. Nesses casos, apenas iremos realizar o tratamento desses dados de acordo com alguma das bases legais disponveis no artigo 11 da LGPD.</Typography>
                <Typography variant='TypographyParagraph' color='#323232'> Alm disso, durante a prestao de nossos servios, podemos tambm coletar dados de menores de 12 anos. Nesses casos, sempre buscaremos a autorizao de seu representante legal ou de um de seus pais, conforme regra presente no artigo 14 da LGPD.</Typography>
            </div>
            <div className={styles.divAdjust}>
                <Typography variant='TypographyParagraph' color='#323232' id={styles.boldAdjust}><li>A Escola Germinare compartilha meus dados com algum?</li></Typography>
                <Typography variant='TypographyParagraph' color='#323232'>A Escola Germinare no compartilha, vende ou transfere seus dados pessoais para nenhuma empresa ou individuo, exceto nas seguintes hipteses:                </Typography>
                <Typography variant='TypographyParagraph' color='#323232'><li>Podemos compartilhar seus dados pessoais a fim de cumprir com as legislaes vigentes (Por exemplo, compartilhamento de dados ao Ministrio da Educao  MEC e a Secretria da Educao do Estado de So Paulo);</li></Typography>
                <Typography variant='TypographyParagraph' color='#323232'><li>Podemos compartilhar seus dados pessoais para hospedagem em plataformas de tecnologia de nossos parceiros;</li></Typography>
                <Typography variant='TypographyParagraph' color='#323232'><li>Garantir a disponibilidade de nossos servios por meio de nosso site</li></Typography>
            </div>
            <div className={styles.divAdjust}>
                <Typography variant='TypographyParagraph' color='#323232' id={styles.boldAdjust}><li>Por quanto tempo iremos reter seus dados?</li></Typography>
                <Typography variant='TypographyParagraph' color='#323232'>Ns manteremos seus dados pessoais somente pelo tempo que for necessrio para cumprir com as finalidades para as quais os coletamos, inclusive para fins de cumprimento de quaisquer obrigaes legais, contratuais, de prestao de contas ou requisio de autoridades competentes.</Typography>
                <Typography variant='TypographyParagraph' color='#323232'>Para determinar o perodo de reteno adequado para os dados pessoais, consideramos a quantidade, a natureza e a sensibilidade dos dados pessoais, o risco potencial de possveis incidentes de segurana que possam ocorrer, a finalidade de processamento dos seus dados pessoais e se podemos alcanar tais propsitos atravs de outros meios, e os requisitos legais aplicveis.</Typography>
            </div>
        </div>
    )
}



