import * as React from 'react';
import { useState } from 'react'
import AcademicContent from '../AcademicContent/AcademicContent'
import {img_seara, img_swift, img_friboi, img_picpay, img_original} from '../Tabs/Assets/Images'
import {useWindowDimensions} from '../../../../hooks';

import { StyledTab, StyledTabs } from './Tabs.style'

type TabPanelProps = {
    children?: React.ReactNode;
    index: number;
    value: number;
}

function TabPanel(props: TabPanelProps) {
    const { children, value, index } = props;

    return (
        <div
            hidden={value !== index}
        >
            {value === index && (
                <div>
                    {children}
                </div>
            )}
        </div>
    );
}

export function AcademicsTabs() {

    const { width } = useWindowDimensions()

    const [value, setValue] = useState(0);

    const handleChange = (event: React.SyntheticEvent, newValue: number) => {
        setValue(newValue);
    };

    const chipsSeara = ['Marketing', 'Marcas', 'Bens de Consumo', 'Inovao']

    return (
        <>
            <div>
                <div>
                    <StyledTabs value={value} onChange={handleChange}>
                        <StyledTab label='Academia Seara' />
                        <StyledTab label="Academia Swift" />
                        <StyledTab label="Academia Friboi" />
                        <StyledTab label="Academia PicPay" />
                        <StyledTab label="Academia Original" />
                    </StyledTabs>
                </div>
                <TabPanel value={value} index={0}>
                    <AcademicContent src={img_seara} title='Academia Seara' chips={['Marketing', 'Marcas', 'Bens de Consumo', 'Inovao']} description='A Academia de Bens de Consumo busca formar tocadores de negcio e, por meio da Metodologia de Casos de Ensino, trabalha o desafio do negcio como instrumento de aprendizagem sobre a complexidade organizacional. Com execues prticas, leva para a sala de aula situaes reais e conhecimento sobre as marcas, sobre as reas do comercial, do marketing, do produto e do Supply Chain.' descriptionWidth={width <= 768 ? '300px' : '569px'} />
                </TabPanel>
                <TabPanel value={value} index={1}>
                    <AcademicContent src={img_swift} title='Academia Swift' chips={['Varejo', 'Supply Chain', 'Vendas']} description='A Academia Swift transforma a sala de aula em um ambiente de negcios, no qual desde o sexto ano os alunos cultivam uma desenvoltura comercial, alm da postura comunicadora e oportunista. Por meio de profissionais atuantes no varejo, nossos estudantes tem acesso  diversas tcnicas e perspectivas sobre o tema, o que os permitem se tornarem especialistas em vendas.' descriptionWidth={width <= 768 ? '300px' : '569px'} />
                </TabPanel>
                <TabPanel value={value} index={2}>
                    <AcademicContent src={img_friboi} title='Academia Friboi' chips={['Commodities', 'Anlise', 'Operaes e Logstica']} description='A Academia Friboi tem como foco preparar os alunos para o core business do grupo, a manufatura de commodities e alimentos, oferencendo aulas magnas, de anlise, de estudo cientfico e relizando peridicas visitas as instalaes da JBS. Alm disso, os estudantes tem acesso a cases reais de ensino, o que propicia uma profunda anlise em aula e um preparo prtico do negcio.' descriptionWidth={width <= 768 ? '300px' : '569px'} />
                </TabPanel>
                <TabPanel value={value} index={3}>
                    <AcademicContent src={img_picpay} title='Academia PicPay' chips={['Tecnologia', 'Produtos Digitais', 'Programao']} description='Nossa instituio  100% digital e preza muito pelo acompanhamento das novas tecnologias. Por isso, a Academia PicPay acredita na formao de profissionais da rea de TI (Tecnologia da Informao) e oferece a nossos alunos aulas de programao desde o sexto ano, incluindo contedos como: Python, HMTL, CSS, JavaScript, anlise de dados e UX.' descriptionWidth={width <= 768 ? '300px' : '569px'} />
                </TabPanel>
                <TabPanel value={value} index={4}>
                    <AcademicContent src={img_original} title='Academia Original' chips={['Finanas', 'Estatstica', 'Mercado de Capitais']} description='A Academia Original estima a formao de estudantes capacitados quanto a matemtica e as finanas. Por meio das aulas de economia e matemtica financeira, nossos alunos desenvolvem capacidades tcnicas e analticas, estimulando sua viso 360 e aprimorando seu perfil quanto ao mercado monetrio.' descriptionWidth={width <= 768 ? '300px' : '569px'} />
                </TabPanel>
            </div>
        </>
    )
}