import './style.scss'
import { Error404 } from '../Error404/index'
import HomeMission from './Assets/Images/HomeMission.png'
import { Typography } from '../../Components/Typography'
import {Carousel, BeliefSection, SuportSection, ContactForms, CTATourVirtual} from './Components'
export function Home() {

    return (
        <section className='HomePage'>
            <Carousel />
            <div className='HomeMissionSection mainContainer'>
                <section>
                    <div className='HomeTextSection'>
                        <Typography color='#2E3092' width='225px' variant='TypographySubtitle'>
                            Nossa Misso
                        </Typography>

                        <Typography variant='TypographyParagraph'>
                            Formar, de maneira efetiva, jovens administradores de empresa que tenham total aderncia aos nossos valores, alta capacidade de liderana, aguado tino comercial e competncia para produzir resultados superlativos.
                        </Typography>
                    </div>

                    <div className='HomeTextSection'>
                        <Typography color='#2E3092' width='320px' variant='TypographySubtitle'>
                            Projeto Acadmico
                        </Typography>

                        <Typography  variant='TypographyParagraph'>
                            O foco principal  formar de maneira efetiva jovens gestores e lderes para o mercado de trabalho, oferecendo ampla preparao com contedos dirigidos ao desenvolvimento de habilidades profissionais e a oportunidade de especializao escolhida entre os seguintes negcios: digital, financeiro, varejo, commodities e bens de consumo.
                        </Typography>
                    </div>
                </section>
                <img src={HomeMission} alt="" />
            </div>
            <BeliefSection />

            <div className="mainContainer">
                <CTATourVirtual />
                <SuportSection/>
                <div className='FormsSection'>
                    <Typography variant='TypographySubtitle' color='#2E3092'>
                        Formulrio de Contato
                    </Typography>
                    <ContactForms />
                </div>
            </div>
        </section>
        
    )
}