import { WelcomeBanner } from "../../Components/WelcomeBanner";
import { Typography } from "../../Components";
import { banner } from "./Assets/Images";
import { Academys, Internship, SchoolRegiment } from "./Components";
import "./style.scss";
export function Academic() {
  return (
    <>
      <WelcomeBanner
        bannerSrc={banner}
        title="Ensino de excelncia e que te prepara para o mercado!"
        titleWidth="58%"
      />
      <main className="mainContainer" id="academic">
        <section>
          <Typography variant="TypographySubtitle" color="#2E3092">
            Como funcionam nossos cursos?
          </Typography>
          <Typography variant="TypographyParagraph">
            A escola Germinare tem como misso formar{" "}
            <strong>jovens administradores de empresas e cidados</strong> por
            meio de uma educao nica e inteiramente gratuita.  uma
            instituio aprovada pelo MEC  Ministrio da Educao  e
            reconhecida pela UNESCO.<br></br>
            <br></br>O foco principal  formar de maneira efetiva jovens
            gestores e <strong>lderes para o mercado de trabalho</strong>,
            oferecendo ampla preparao com contedos dirigidos ao
            desenvolvimento de{" "}
            <strong>
              habilidades profissionais e a oportunidade de especializao
            </strong>{" "}
            escolhida entre os seguintes negcios: digital, financeiro, varejo,
            commodities e bens de consumo.
          </Typography>
        </section>
        <section>
            <Typography variant="TypographySubtitle" color="#2E3092">Disciplinas focadas em gesto de negcios</Typography>
            <Typography variant="TypographyParagraph">O Instituto Germinare, alm de oferecer toda a base curricular do MEC, oferecendo matrias bsicas como: portugus, matemtica, histria, fsica, qumica e biologia contm em sua estrutura <strong>matrias voltadas ao preparo do aluno como profissional</strong>, fazendo com que assim ele esteja preparado para o mercado de trabalho.<br></br><br></br><strong>Clique em cada seo para saber mais.</strong></Typography>
            <Academys></Academys>
            <Internship></Internship>
        </section>
        <SchoolRegiment></SchoolRegiment>
      </main>
    </>
  );
}
