import { Typography } from "../../../../Components";
import styles from './Internship.module.scss'
export function Internship() {
  return (
    <div className={styles.container}>
      <Typography variant="TypographySubtitle" style={{fontSize: 28, color: '#2E3092'}}>Estgios Remunerados</Typography>
      <Typography variant="TypographyParagraph" color="#323232" id={styles.internship}>Os <strong>alunos iniciam estgios j no segundo semestre do 9 ano</strong>, como forma de aplicar no mundo empresarial os conhecimentos adquiridos em sala de aula. Esta  uma das estratgias da escola para que que os alunos tomem conhecimentos e demandas do mundo real, interessem-se, entendam a importncia e dediquem-se para superar as expectativas.</Typography>
    </div>
  );
}
