export function Governance() {
  return (
    <>
      <WelcomeBanner
        title="Nosso compromisso com voc!"
        bannerSrc={Banner}
        titleWidth="540px"
        id="governanceWelcomeBanner"
      ></WelcomeBanner>
      <div className="PLStatement-all-container">
        <Typography variant="TypographyParagraph" color="#323232">
          O modelo de Governana Corporativa da Escola Germinare, instituio
          sem fins lucrativos, visa atender aos mais altos padres de
          organizaes pblicas ou privadas como forma de orientar nossa atuao
          a partir das boas prticas do setor educacional. Nossas aes e nosso
          plano diretivo so orientados com base em nossa misso, crena e
          valores. Buscamos contribuir com a sociedade, oferecendo ensino de
          alta qualidade com o propsito de formar lderes em negcios, que
          sero os futuros gestores do nosso pas. Alm do ensino gratuito, os
          alunos recebem refeies dirias, uniforme e todo o material didtico.
        </Typography>
        <Typography
          variant="TypographyParagraph"
          width="1146.64px"
          color="#323232"
        >
          Como forma de manter a transparncia junto a sociedade e apoiadores,
          publicamos abaixo os demonstrativos financeiros da Escola Germinare:
        </Typography>
        <PLStatement documents={documents} />
        <SimpleText />
        <div className="PLStatement-div">
          <PLModal />
        </div>
        <TopicText />
      </div>
    </>
  );
}
